<template>
    <div>
        <v-flex xs-12 mb-2 px-3 pt-5>
            <span class="display-1">Login to account</span>
        </v-flex>
        <form action="" @submit.prevent="validateBeforeSubmit">
            <div class="ea-form-group px-3">
                <label for="email">Email * </label>
                <input autocomplete="off" type="email" v-model="user.email" name="email" id="email" v-validate="'required|email'" :class="{'ea-form-control': true, 'error-form': errors.has('email')}" placeholder="Name">
                <div style="height: 1rem">
                    <span v-show="errors.has('email')" class="ea-error-helper caption">{{ errors.first('email') }}</span>
                </div>
            </div>
            <div class="ea-form-group px-3">
                <label for="password">Password * </label>
                <input autocomplete="off" v-validate="'required|min:8|max:100'" v-model="user.password" name="password" type="password" id="password" :class="{'ea-form-control': true, 'error-form': errors.has('password')}" placeholder="Password">
                <div style="height: 1rem">
                    <span v-show="errors.has('password')" class="ea-error-helper caption">{{ errors.first('password') }}</span>
                </div>
            </div>
            <div class="ea-form-group ma-0 px-3">
                <a href="" class="ea-link text-xs-right">Forgot password?</a>
            </div>
            <div class="ea-form-group px-3">
                <v-btn type="submit" color="purple" class="white--text" block>Sign In</v-btn>
            </div>
        </form>
        <login-social />
    </div>
</template>

<script>
import LoginSocial from '../components/LoginSocial'
export default {
    name: "LoginForm",
    components: {
        LoginSocial
    },
    data(){
        return{
            user: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        validateBeforeSubmit() {
            this.$validator.validateAll().then((result) => {
                if (result) {
                // eslint-disable-next-line
                alert('Form Submitted!');
                return;
                }

            });
        }
    }
}
</script>

