<template>
    <v-container grid-list-xs justify-center align-content-space-around>
        <v-layout row wrap>
            <v-flex xs-12  mt-5 mb-3 px-3>
                <span class="display-1">Send EA BeautyLine A Message</span>
            </v-flex>
            <v-flex xs12>
                <form action="" @submit.prevent="validateBeforeSubmit">
                    <div class="ea-form-group px-3">
                        <label for="email">Email * </label>
                        <input type="email" v-model="contact.email" name="email" id="email" v-validate="'required|email'" :class="{'ea-form-control': true, 'error-form': errors.has('email')}" placeholder="Name">
                        <div style="height: 1rem">
                            <span v-show="errors.has('email')" class="ea-error-helper caption">{{ errors.first('email') }}</span>
                        </div>
                    </div>
                    <div class="ea-form-group px-3">
                        <v-container grid-list-xs>
                            <v-layout row wrap justify-space-between>
                                <v-flex xs12>
                                    <div>Subject *</div>
                                </v-flex>
                                <v-flex xs12>
                                    <input v-model="contact.subject" v-validate="'required'" type="text" id="subject" name="subject" placeholder="How may we help you . . ." :class="{'ea-form-control': true, 'error-form': errors.has('subject')}">
                                </v-flex>
                            </v-layout>
                        </v-container>
                        <div style="height: 1rem">
                            <span v-show="errors.has('firstname') || errors.has('lastname')" class="ea-error-helper caption">{{ errors.first('firstname') || errors.first('lastname') }}</span>
                        </div>
                    </div>
                    <div class="ea-form-group px-3">
                        <label for="password">Message * </label>
                        <textarea v-validate="'required|min:8|max:100'" v-model="contact.message" name="message" id="message" :class="{'ea-form-control': true, 'error-form': errors.has('message')}" placeholder="Your message . . ."></textarea>
                        <div style="height: 1rem">
                            <span v-show="errors.has('message')" class="ea-error-helper caption">{{ errors.first('message') }}</span>
                        </div>
                    </div>
                    <div class="ea-form-group px-3">
                        <v-btn type="submit" :disabled="errors.items.length > 0" color="purple" class="white--text" block>Send Message</v-btn>
                    </div>
                </form>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
    name: "Contact",
    data(){
        return{
            form: true,
            contact: {
                email: '',
                subject: '',
                message: '',
            }
        }
    },
    methods: {
        validateBeforeSubmit() {
            this.$validator.validateAll().then((result) => {
                if (result) {
                // eslint-disable-next-line
                alert('Form Submitted!');
                return;
                }

            });
        }
    }
}
</script>

