<template>
    <v-container grid-list-xs justify-center align-content-space-around>
            <v-layout row wrap justify-center>
                <v-flex xs12 md7>
                    <v-card class="py-5" flat>
                        <v-flex xs12 pt mb-3 px-3>
                            <span class="display-1">Register an account</span>
                        </v-flex>
                        <v-flex xs12>
                            <v-divider></v-divider>
                        </v-flex>
                        <form action="" @submit.prevent="validateBeforeSubmit">
                            <div class="ea-form-group px-3">
                                <label for="email">Email * </label>
                                <input type="email" v-model="user.email" name="email" id="email" v-validate="'required|email'" :class="{'ea-form-control': true, 'error-form': errors.has('email')}" placeholder="Name">
                                <div style="height: 1rem">
                                    <span v-show="errors.has('email')" class="ea-error-helper caption">{{ errors.first('email') }}</span>
                                </div>
                            </div>
                            <div class="ea-form-group px-3">
                                <v-container grid-list-xs pa-0>
                                    <v-layout row wrap justify-space-between>
                                        <v-flex xs12>
                                            <div>Name *</div>
                                        </v-flex>
                                        <v-flex xs6>
                                            <input v-model="user.firstname" v-validate="'required'" type="text" id="firstname" name="firstname" placeholder="First Name" :class="{'ea-form-control': true, 'error-form': errors.has('firstname')}">
                                        </v-flex>
                                        <v-flex xs6>
                                            <input v-model="user.lastname" v-validate="'required'" type="text" id="lastname" name="lastname" :class="{'ea-form-control': true, 'error-form': errors.has('lastname')}" placeholder="Last Name">
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                                <div style="height: 1rem">
                                    <span v-show="errors.has('firstname') || errors.has('lastname')" class="ea-error-helper caption">{{ errors.first('firstname') || errors.first('lastname') }}</span>
                                </div>
                            </div>
                            <div class="ea-form-group px-3">
                                <label for="password">Password * </label>
                                <input v-validate="'required|min:8|max:100'" v-model="user.password" name="password" type="password" id="password" :class="{'ea-form-control': true, 'error-form': errors.has('password')}" placeholder="Password">
                                <div style="height: 1rem">
                                    <span v-show="errors.has('password')" class="ea-error-helper caption">{{ errors.first('password') }}</span>
                                </div>
                            </div>
                            <div class="ea-form-group px-3">
                                <v-container grid-list-xs pa-0>
                                    <v-layout row wrap justify-space-between>
                                        <v-flex xs12>
                                            <div>Phone Number *</div>
                                        </v-flex>
                                        <v-flex xs2>
                                            <input type="text" disabled class="ea-form-control pa-1 text-xs-center" value="+234">
                                        </v-flex>
                                        <v-flex xs10>
                                            <input v-validate="'required|numeric'" v-model="user.phoneno" type="text" name="phoneno" id="phoneno" class="ea-form-control" :class="{'ea-form-control': true, 'error-form': errors.has('phoneno')}" placeholder="7012345678">
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                                <div style="height: 1rem">
                                    <span v-show="errors.has('phoneno')" class="ea-error-helper caption">{{ errors.first('phoneno') }}</span>
                                </div>
                            </div>
                            <!-- <div class="ea-form-group mt-2 px-3">
                                <v-container grid-list-xs pa-0>
                                    <v-layout row wrap>
                                        <v-flex xs1 text-xs-left>
                                            <input type="checkbox" v-model="user.checked" class="ea-form-control pa-0">
                                        </v-flex>
                                        <v-flex xs11>
                                            <div class="body-1">
                                                By registering, you are agreeing to eabeautyLine terms and conditions.
                                            </div>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </div> -->
                            <div class="ea-form-group px-3">
                                <v-btn type="submit" @click="check()" color="purple" class="white--text" block>Register</v-btn>
                            </div>
                        </form>
                        <login-social />
                    </v-card>
                </v-flex>
            </v-layout>
    </v-container>
</template>

<script>
import LoginSocial from '../components/LoginSocial'
import axios from 'axios'
export default {
    name: "Register",
    components: {
        LoginSocial
    },
    data(){
        return{
            form: true,
            user: {
                email: '',
                firstname: '',
                lastname: '',
                password: '',
                phoneno: '',
                checked: false
            }
        }
    },
    methods: {
        validateBeforeSubmit() {
            this.$validator.validateAll().then((result) => {
                if (result) {
                // eslint-disable-next-line
                alert('Form Submitted!');
                return;
                }

            });
        },
        check(){
            axios.get('/api/user').then((data) => {
                console.log(data);
            });
        }
    }
}
</script>

